<script>
  function createHideInput(name, value){
    var newElement = document.createElement("input");
    newElement.setAttribute("name",name);
    newElement.setAttribute("type","hidden");
    newElement.setAttribute("value",value);
    return newElement;
  }
  export default {
    data () {
      return {
        abc:'',
      }
    },
    filters: {
      filtersDome(date){

      },
    },
    watch: {


    },
    created: function () {
      this.goPay()
    },
    mounted:function(){

    },

    methods: {
      goPay(){
        console.log(this.$route.params.code);
        console.log(this.$route.query.token);
        this.$ajax.post('/hotel/order/getPayParam', {'orderCode':this.$route.params.code,token:this.$route.query.token}).then(function (res) {
          if (res.data.result == 1) {
//            getnotice('支付跳转开始')
            console.log('test');
            var payForm = document.createElement("form");
            document.body.appendChild(payForm);
            payForm.method = 'post';
            payForm.action = 'http://www.fangcang.com/titanjr-pay-app/titanPay.action';
            payForm.appendChild(createHideInput("payType", data.model.payType));//收付款方编码类型
            payForm.appendChild(createHideInput("payeeOrg", data.model.payeeOrg));//收款机构
            payForm.appendChild(createHideInput("payeeOrgName", data.model.payeeOrgName));//收款机构名称
            payForm.appendChild(createHideInput("payerOrgName", data.model.payerOrgName));//付款机构名称
            payForm.appendChild(createHideInput("commodityName", data.model.commodityName));//商品名称
            payForm.appendChild(createHideInput("commodityDesc", data.model.commodityDesc));//商品描述
            payForm.appendChild(createHideInput("bussOrderNo", data.model.bussOrderNo));//商品编号，可以是对方的订单号
            payForm.appendChild(createHideInput("currencyType", data.model.currencyType));//币种
            payForm.appendChild(createHideInput("amount", data.model.amount));//订单金额
            payForm.appendChild(createHideInput("channelType", data.model.channelType));// 支付通道类型
            payForm.appendChild(createHideInput("notifyUrl", data.model.notifyUrl));// 支付通道类型
            payForm.appendChild(createHideInput("bussInfo", data.model.bussInfo));//业务信息(传给金融的信息，原封传回)
            payForm.appendChild(createHideInput("successJumpUrl", data.model.successJumpUrl));//支付成功处理
            payForm.appendChild(createHideInput("failJumpUrl", data.model.failJumpUrl));//支付失败处理
            payForm.appendChild(createHideInput("escrowedDate", data.model.escrowedDate));//解冻日期
            payForm.appendChild(createHideInput("sign", data.model.sign));//签名
            payForm.submit();
          }
        }).catch(function (err) {

        });
      },
    }
  }
</script>



